
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color:burlywood;
  color:black;
  padding: 10px 20px;
}

.logo {
  display: flex;
  align-items: center;
}

.logo img {
  width: 40px;
  height: auto;
  margin-right: 10px;
}

.navLinks {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.navLinks li {
  display: inline;
  margin-right: 20px;
}

.navLinks li a {
  color:black;
  text-decoration: none;
  font-size: 18px;
  font-weight: bold;
  transition: color 0.3s ease;
}

.navLinks li a:hover {
  color: #ff6347;
}
.homeContainer {
  position: relative;
}

.homeImage {
  width: 100%;
  height:750px;
}

.homeText {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  color:white;
}
.aboutContainer {
  display: flex;
  justify-content: space-between;
  background-color: beige;
  text-align:center;
}s

.aboutText {
  padding: 20px;
}

.aboutImage {
  flex: 1;
  width: 1000%;
  height: 700px;
}
.cuisineContainer {
  padding: 100px;
  background-color: beige;
  
}
.cuisineh1{
  text-align: center;
}

.cuisineList {
  display: flex;
  flex-wrap: wrap;
}
.cuisineItem {
  width: calc(45% - 20px); 
  margin: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  overflow: hidden;
  background-color: bisque;
}

.cuisineImage {
  width: 100%; 
  max-height: 300px;
  object-fit: cover; 
}

.cuisineInfo {
  padding: 10px;
}

.cuisineInfo h2 {
  margin-top: 0;
  align-items: center;
}

.cuisineLink {
  display: block;
  margin-top: 10px;
  color:burlywood;
  text-decoration: none;
}

.cuisineLink:hover {
  text-decoration: underline;
}



