/* Contact.module.css */

.contactContainer {
    padding: 150px;
    background-color: beige;
  }
  
  .contactForm {
    margin-bottom: 30px;
  }
  
  .formGroup {
    margin-bottom: 15px;
  }
  
  label {
    display: block;
    margin-bottom: 5px;
  }
  
  input,
  textarea {
    width: 100%;
    padding: 8px;
    border: 1px solid #ddd;
    border-radius: 5px;
  }
  
  button {
    padding: 10px;
    background-color:beige;
    color: #333;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  button:hover {
    background-color: black;
  }
  
  .socialLinks {
    text-align: center;
  }
  
  .socialLinks h2 {
    margin-bottom: 10px;
  }
  
  .socialLinks ul {
    list-style-type: none;
    padding: 0;
  }
  
  .socialLinks ul li {
    display: inline;
    margin-right: 10px;
  }
  
  .socialLinks ul li a {
    color: black;
    text-decoration: none;
  }
  
  .socialLinks ul li a:hover {
    text-decoration: underline;
  }
  